.formBlockContainer {
    display: flex;
    justify-content: center;   
}

.formBlock {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 60px;
    margin-bottom: 40px;
    background-color: rgba(185, 236, 255, 0.82);
    flex-direction: column;
}

.formApplication {
    align-items: center;
    margin-bottom: 10px;
    width: 500px;
}

.formLogin, .formPassword {
    width: 100%;
    height: 30px;
    margin-bottom: 30px;
}

.InputMain {
    color: black;
    margin-bottom: 5px;
    border: none;
    outline: none;
    font-size: 18px;
    font-weight: 200;
    width: 100%;
    height: 100%;
    line-height: 20px;
    padding-left: 5px;
    border-bottom: 2px solid rgb(95, 73, 185);
    background-color: rgba(123, 110, 120, 0.36);
}

.formLoginLabel, .formPasswordLabel, .formRegLabel {
    padding-left: 5px;
    font-size: 16px;
}

.button {
	background-color:#a866ff;
	border-radius:8px;
	cursor:pointer;
	color:#ffffff;
    font-size:18px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    letter-spacing: 2px;
    padding:10px 20px;
    border: 1px solid white;
}
.button:hover {
	background-color:#917ac2;
}

.textLog {
    color: black;
    font-size: 16px;
}

@media only screen and (max-width: 1000px)
{
    .formBlockContainer{
        width: 80%;
    }

    .formApplication{
        width: 90%;
    }
}